.darkmode {
  background-color: black;
  color: white;
}
.darkmode .ant-layout {
  background-color: black;
}
.darkmode .ant-card-body {
  background-color: #141414;
  align-items: center;
}
.darkmode .ant-layout-header {
  background-color: #1f1f1f;
}

.darkmode .MuiTypography-root {
  color: white;
}

.darkmode .MuiFormLabel-root {
  color: white;
}

.darkmode .MuiOutlinedInput-notchedOutline {
  border-color: white;
}

.darkmode .MuiOutlinedInput-input {
  color: white;
}
.darkmode .ant-table.ant-table-small {
  font-size: 14px;
  background-color: #141414;
  color: white;
}

.darkmode .ant-layout-content {
  min-height: 546px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.darkmode .ant-statistic-title {
    color: white;
}
.darkmode .ant-statistic-content {
    color:white;
}